ación que es el último rito del servicio y se realiza al lado de la tumba o el nicho del cementerio, una vez finalizada se procede a realizar el entierro o la cremación.")]),_c('ul',{staticClass:"lista-ul ms-lg-5 mb-0"},[_c('li',[_c('i',{staticClass:"fas fa-play",staticStyle:{"color":"#008F85"}}),_c('p',{staticClass:"mb-0"},[_vm._v("Al 7mo y al 30avo día del fallecimiento se realiza la misa de los difuntos con el fin de rezar por el alma de los difuntos.")])])])]),_c('div',{staticClass:"row justify-content-center mb-4"},[_c('h4',[_vm._v("Ritual de luto")]),_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',{staticClass:"mb-4"},[_vm._v("La familia directa del fallecido deberá vestir ropa color negro, los acompañantes al velorio deberán hacer llegar flores y suele rezarse el rosario en el tiempo de la velación.")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('ul',{staticClass:"lista-ul"},[_c('li',[_c('i',{staticClass:"fas fa-play",staticStyle:{"color":"#008F85"}}),_c('p',{staticClass:"mb-0"},[_vm._v("Se recomienda evitar las reuniones sociales, fiestas y actividades placenteras durante las primeras semanas posteriores al fallecimiento.")])]),_c('li',[_c('i',{staticClass:"fas fa-play",staticStyle:{"color":"#008F85"}}),_c('p',{staticClass:"mb-0"},[_vm._v("No hay tiempo determinado para el luto.")])])])])])]),_c('div',{staticClass:"col-6 col-lg-4"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"350px"},attrs:{"src":require("@/assets/curso/tema4/img12.png"),"alt":"imagen ambiental"}})])])])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Hindú"}},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-6 col-md-4 mb-4 mb-md-0 align-self-center"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"349px"},attrs:{"src":require("@/assets/curso/tema4/img13.png"),"alt":"imagen ambiental"}})])]),_c('div',{staticClass:"col-md-